(function(scope) {
    scope.canvas = undefined;
    scope.context = undefined;
    scope.tempCanvas = undefined;
    scope.tempContext = undefined;
    scope.checkPoint = undefined;
    scope.image = undefined;
    scope.video = undefined;
    scope.vWidth = undefined;
    scope.vHeight = undefined;
    scope.streaming = undefined;
    scope.streamInterval = undefined;
    scope.isStreaming = false;
    scope.current = "Original";
    scope.brightnessV = 0;
    scope.contrastV = 0;
    scope.noiseV = 0;
    scope.saturationV = 1;
    scope.vignette = false;
    scope.effect = 'none';
    scope.degrees = 0;
    scope.file_extension = 'png';
    scope.setType = function(type)
    {
        window.file_extension = type;
        window.$('.ext').removeClass("active-link");
        window.$('.'+type).addClass("active-link");
    }
})(window);



