import $ from './lib/jquery.min';
require("imports-loader?jQuery=jquery!touchSwipe");
require("imports-loader?jQuery=jquery!perfectScrollbar");
import {
      filters,
      selection
} from './filters';
import Commun from './Commun';
import Render from './Render';
import Ui from './Ui';
import App from './App';
import Api from './Api';

var app_object = new App;

// window Variables
import './variables';
window.filters = filters;
window.Ui = Ui;
window.App = app_object;
window.Api = Api;
window.Render = Render;
window.$ = $;

$(function () {
      window.canvas = document.getElementById('new');
      window.image = document.getElementById('old');
      var src = Commun.getUrlParam();
      if (src == undefined) {
            window.image.src = 'assets/img/pictures/Default3.jpg';
            window.image.onload = function () {
                  Render.initCanvas();
            }
      } else if (src == "null") {;
      } else {
            window.image.src = src;
            window.image.onload = function () {
                  Render.initCanvas();
            }
      }
      window.video = document.getElementById('video');
      window.canvas.addEventListener("dragenter", Api.dragenter, false);
      window.canvas.addEventListener("dragover", Api.dragover, false);
      window.canvas.addEventListener("drop", Api.drop, false);
      window.video.addEventListener('canplay', function () {
            if (!window.isStreaming) {
                  window.vWidth = window.video.videoWidth;
                  window.vHeight = window.video.videoHeight;
                  window.canvas.setAttribute('width', window.vWidth);
                  window.canvas.setAttribute('height', window.vHeight);
                  Render.canvasPos();
                  window.context.translate(window.vWidth, 0);
                  window.context.scale(-1, 1);
                  $(".interaction").hide();
                  $("#undo").hide();
                  $("#check").hide();
                  $(".nav").hide();
                  $("#take").show();
                  $("#package-icon").show();
                  $("#filters").addClass("toright");
                  Ui.showCSettings();
                  window.isStreaming = true;
            }
      }, false);
      window.video.addEventListener('play', function () {
            window.streamInterval = setInterval(function () {
                  if (window.video.paused || window.video.ended) return;
                  window.context.fillRect(0, 0, window.vWidth, window.vHeight);
                  window.context.drawImage(window.video, 0, 0, window.vWidth, window.vHeight);
                  app_object.applyFilter(window.current);
                  app_object.applyBrightness(window.brightnessV);
                  app_object.applyContrast(window.contrastV);
                  app_object.applySaturation(window.saturationV);
                  app_object.applyNoise(window.noiseV);
                  if (window.vignette)
                        app_object.applyVignette();
                  if (window.effect == 'Grayscale' && window.effect != 'none')
                        app_object.applyGrayscale();
                  if (window.effect == 'Sepia' && window.effect != 'none')
                        app_object.applySepia();
                  if (window.effect == 'Invert' && window.effect != 'none')
                        app_object.applyInvert();
            }, 33);
      }, false);
      $("#new").swipe({
            swipeLeft: function () {
                  if (selection.indexOf(window.current) < selection.length - 1) {
                        window.current = selection[selection.indexOf(window.current) + 1];
                        app_object.applyFilter(window.current);
                  } else if (selection.indexOf(window.current) == selection.length - 1) {
                        window.current = selection[0];
                        app_object.applyFilter(window.current);
                  }
            },
            swipeRight: function () {
                  if (selection.indexOf(window.current) > 0) {
                        window.current = selection[selection.indexOf(window.current) - 1];
                        app_object.applyFilter(window.current);
                  } else if (selection.indexOf(window.current) == 0) {
                        window.current = selection[selection.length - 1];
                        app_object.applyFilter(window.current);
                  }
            },
            threshold: 0
      });
      $(".interaction").on('click', function () {
            $("." + "active").each(function () {
                  $(this).removeClass("active");
            })
            $(this).addClass("active");
            $("#back").hide();
      });
      $(".dim").on('click', function () {
            let size = parseInt($(this).attr('data-size'),10);
            let width = window.canvas.width*size/100;
            let height = window.canvas.height*size/100;
            var exportCanvas = document.createElement('canvas');
            exportCanvas.width = width;
            exportCanvas.height = height;
            exportCanvas.getContext('2d').drawImage(window.canvas, 0, 0, width, height);
            var data = exportCanvas.toDataURL('image/' + window.file_extension);
            $(this).attr('href',data).attr("download", "newImage-"+size+"."+window.file_extension);
      });
      $(".pos").on('click', function () {
            let position = parseInt($(this).attr('data-position'),10);
            app_object.applyLogo(position);
      });
      $("#share-btn").on('click', function () {
            if (window.Lollipop)
            {
                  $('.share-btn-twitter').attr('href', `https://twitter.com/share?url=${encodeURIComponent(window.Lollipop.getParams().newImage_url)}&text=Lollipop 2.0 &via=Lollipop`);
                  $('.share-btn-facebook').attr('href', `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(window.Lollipop.getParams().newImage_url)}`);
                  $('.share-btn-googleplus').attr('href', `https://plus.google.com/share?url=${encodeURIComponent(window.Lollipop.getParams().newImage_url)}`);
                  $('.share-btn-tumblr').attr('href', `https://www.tumblr.com/share/link?url=${encodeURIComponent(window.Lollipop.getParams().newImage_url)}`);
                  $('.share-btn-linkedin').attr('href', `https://www.linkedin.com/shareArticle?mini=true&url=${encodeURIComponent(window.Lollipop.getParams().newImage_url)}`);
                  $('.share-btn-pinterest').attr('href', `https://www.pinterest.com/pin/create/button/?url=${encodeURIComponent(window.Lollipop.getParams().newImage_url)}`);
                  $('.share-btn-stumbleupon').attr('href', `http://www.stumbleupon.com/submit?url=${encodeURIComponent(window.Lollipop.getParams().newImage_url)}`);
                  $('.share-btn-delicious').attr('href', `https://delicious.com/save?v=5&noui&jump=close&url=${encodeURIComponent(window.Lollipop.getParams().newImage_url)}`);
                  $('.share-btn-reddit').attr('href', `http://www.reddit.com/submit?url=${encodeURIComponent(window.Lollipop.getParams().newImage_url)}`);
            }
      });
      $("#upload-btn").on('click', function () {
            var ext = window.file_extension;
            var dt = window.canvas.toDataURL('image/'+ext);
            if (window.Lollipop) {
                  window.Lollipop.save(dt,ext);
            }
      });
      $('.setting').perfectScrollbar();
      $('.theme').perfectScrollbar();
      $("#filters-icon").addClass("active");
});

$(window).on("resize orientationchange", function () {
      $('.setting').perfectScrollbar('update');
      $('.popup').css('margin-top', ($(window).height() / 2) - ($('.popup').height()));
      Render.canvasPos();
});

$(document).ready(function () {
      $("#loading").css('visibility', 'visible');
})
$(window).on("load", function () {
      $("html").css('visibility', 'visible');
      $("#loading").css('display', 'none');
      $('.popup').css('margin-top', ($(window).height() / 2) - ($('.popup').height()));
})

document.addEventListener("DOMContentLoaded", function () {
      var items = document.querySelectorAll(".animated-search-filter > *")
      var itemHeight = items[0].offsetHeight
      var texts = []
      var i = -1
      var len = items.length
      var transform = "transform" in document.body.style ? "transform" : "webkitTransform"
      while (++i < len) {
            texts.push(items[i].textContent.trim())
            items[i].style[transform] = "translateY(" + i * itemHeight + "px)"
      }
      document.querySelector("input.animated-search-filter").addEventListener("input", function () {
            var re = new RegExp(this.value, "i")
            texts.forEach(function (element, index) {
                  if (re.test(element)) {
                        items[index].classList.remove("hidden")
                  } else {
                        items[index].classList.add("hidden")
                  }
                  var i = -1
                  var position = 0
                  while (++i < len) {
                        if (items[i].className != "hidden") {
                              items[i].style[transform] = "translateY(" + position++ * itemHeight + "px)"
                        }
                  }
            })
      })
      var shareButtons = document.querySelectorAll(".share-btn");
      if (shareButtons) {
            [].forEach.call(shareButtons, function (button) {
                  button.addEventListener("click", function (event) {
                        var width = 650,
                              height = 450;
                        event.preventDefault();
                        window.open(this.href, 'Share Dialog', 'menubar=no,toolbar=no,resizable=yes,scrollbars=yes,width=' + width + ',height=' + height + ',top=' + (screen.height / 2 - height / 2) + ',left=' + (screen.width / 2 - width / 2));
                  });
            });
      }
})


