import Render from './Render';
import StackBlur from './StackBlur';
import CompoundBlur from './CompoundBlur';

export default class App {
    constructor() {
        this.blurRadius = 32;
        this.radiusFactor = 1.5;
        this.divider = this.radiusFactor;
        this.startRadius = this.blurRadius / this.divider;
        this.steps = 3;
        this.sb_object = new StackBlur;
        this.cb_object = new CompoundBlur;
    }

    applyLogo(position) {
        let stick = new Image();
        stick.src = "assets/img/" + window.logoUrl;
        stick.onload = function () {
            //Render.loadCheckPoint();
            switch (position) {
                case 0:
                    window.context.drawImage(stick, 0, 0, stick.width, stick.height, window.canvas.width - stick.width - 10, window.canvas.height - stick.height - 10, stick.width, stick.height);
                    break;
                case 45:
                    window.context.drawImage(stick, 0, 0, stick.width, stick.height, window.canvas.width - stick.width -10 , 10, stick.width, stick.height);
                    break;
                case 135:
                    window.context.drawImage(stick, 0, 0, stick.width, stick.height, 10, 10, stick.width, stick.height);
                    break;
                case 180:
                    window.context.drawImage(stick, 0, 0, stick.width, stick.height, 10, window.canvas.height - stick.height -10, stick.width, stick.height);
                    break;
                default:
                    break;
            }
        }
    }

    applyNewCrop() {
        var tempCanvas = window.cropper.getCroppedCanvas();
        window.canvas.width = tempCanvas.width;
        window.canvas.height = tempCanvas.height;
        window.context.drawImage(tempCanvas, 0, 0);
        window.cropper.destroy();
    }

    applyFilter(filter) {
        if (!window.isStreaming)
            Render.loadCheckPoint();
        window.current = filter;
        var imageData = window.context.getImageData(0, 0, window.canvas.width, window.canvas.height),
            rgba = imageData.data,
            length = rgba.length;
        for (let i = 0; i < length; i += 4) {
            rgba[i] = window.filters[filter].r[rgba[i]];
            rgba[i + 1] = window.filters[filter].g[rgba[i + 1]];
            rgba[i + 2] = window.filters[filter].b[rgba[i + 2]];
        }
        //imageData.data = rgba;
        Object.defineProperties(imageData, {
            'data': {
                value: rgba,
                writable: true,
                configurable: true,
                enumerable: true
            }
        });
        window.context.putImageData(imageData, 0, 0);
    }

    applyRotation() {
        window.degrees += 90
        if (window.degrees >= 360) window.degrees = 0;

        if (window.degrees === 0 || window.degrees === 180) {
            window.canvas.width = window.tempCanvas.width;
            window.canvas.height = window.tempCanvas.height;
        } else {
            // swap
            window.canvas.width = window.tempCanvas.height;
            window.canvas.height = window.tempCanvas.width;
        }
        window.context.save();
        window.context.translate(window.canvas.width / 2, window.canvas.height / 2);
        window.context.rotate(window.degrees * Math.PI / 180);
        window.context.drawImage(window.tempCanvas, -window.tempCanvas.width * 0.5, -window.tempCanvas.height * 0.5);
        window.context.restore();
        Render.canvasPos();
    }

    applyFlipH() {
        window.context.save();
        window.context.translate(window.tempCanvas.width, 0);
        window.context.scale(-1, 1);
        window.context.drawImage(window.tempCanvas, 0, 0, window.tempCanvas.width, window.tempCanvas.height, 0, 0, window.canvas.width, window.canvas.height);
        window.context.restore();
    }

    applyBrightness(adjustment) {
        adjustment = parseInt(adjustment, 10);
        window.brightnessV = adjustment;
        if (!window.isStreaming)
            Render.loadCheckPoint();
        let imageData = window.context.getImageData(0, 0, window.canvas.width, window.canvas.height),
            rgba = imageData.data,
            length = rgba.length;
        for (let i = 0; i < length; i += 4) {
            rgba[i] += adjustment;
            rgba[i + 1] += adjustment;
            rgba[i + 2] += adjustment;
        }
        //imageData.data = rgba;
        Object.defineProperties(imageData, {
            'data': {
                value: rgba,
                writable: true,
                configurable: true,
                enumerable: true
            }
        });
        window.context.putImageData(imageData, 0, 0);
    }

    applyContrast(contrast) {
        contrast = parseInt(contrast, 10);
        window.contrastV = contrast;
        if (!window.isStreaming)
            Render.loadCheckPoint();
        let imageData = window.context.getImageData(0, 0, window.canvas.width, window.canvas.height),
            rgba = imageData.data,
            length = rgba.length;
        let factor = (259 * (contrast + 255)) / (255 * (259 - contrast));
        for (let i = 0; i < length; i += 4) {
            rgba[i] = factor * (rgba[i] - 128) + 128;
            rgba[i + 1] = factor * (rgba[i + 1] - 128) + 128;
            rgba[i + 2] = factor * (rgba[i + 2] - 128) + 128;
        }
        //imageData.data = rgba;
        Object.defineProperties(imageData, {
            'data': {
                value: rgba,
                writable: true,
                configurable: true,
                enumerable: true
            }
        });
        window.context.putImageData(imageData, 0, 0);
    }

    applySaturation(saturation) {
        saturation = parseFloat(saturation);
        window.saturationV = saturation;
        if (!window.isStreaming)
            Render.loadCheckPoint();
        let imageData = window.context.getImageData(0, 0, window.canvas.width, window.canvas.height);
        let dA = imageData.data;
        let sv = saturation;
        let luR = 0.3086;
        let luG = 0.6094;
        let luB = 0.0820;
        let az = (1 - sv) * luR + sv;
        let bz = (1 - sv) * luG;
        let cz = (1 - sv) * luB;
        let dz = (1 - sv) * luR;
        let ez = (1 - sv) * luG + sv;
        let fz = (1 - sv) * luB;
        let gz = (1 - sv) * luR;
        let hz = (1 - sv) * luG;
        let iz = (1 - sv) * luB + sv;
        for (let i = 0; i < dA.length; i += 4) {
            let red = dA[i];
            let green = dA[i + 1];
            let blue = dA[i + 2];
            let saturatedRed = (az * red + bz * green + cz * blue);
            let saturatedGreen = (dz * red + ez * green + fz * blue);
            let saturateddBlue = (gz * red + hz * green + iz * blue);
            dA[i] = saturatedRed;
            dA[i + 1] = saturatedGreen;
            dA[i + 2] = saturateddBlue;
        }
        //imageData.data = dA;
        Object.defineProperties(imageData, {
            'data': {
                value: dA,
                writable: true,
                configurable: true,
                enumerable: true
            }
        });
        window.context.putImageData(imageData, 0, 0);
    }

    applyNoise(value) {
        value = parseInt(value, 10);
        window.noiseV = value;
        if (!window.isStreaming)
            Render.loadCheckPoint();
        let imageData = window.context.getImageData(0, 0, window.canvas.width, window.canvas.height);
        let data = imageData.data;
        for (let i = 0; i < data.length; i += 4) {
            let noise = value - Math.random() * value / 2;
            data[i] += noise;
            data[i + 1] += noise;
            data[i + 2] += noise;
        }
        //imageData.data = data;
        Object.defineProperties(imageData, {
            'data': {
                value: data,
                writable: true,
                configurable: true,
                enumerable: true
            }
        });
        window.context.putImageData(imageData, 0, 0);
    }

    applySharpen(mix) {
        Render.loadCheckPoint();
        mix = parseFloat(mix);
        let w = window.canvas.width;
        let h = window.canvas.height;
        let weights = [0, -1, 0, -1, 5, -1, 0, -1, 0],
            katet = Math.round(Math.sqrt(weights.length)),
            half = (katet * 0.5) | 0,
            dstData = window.context.createImageData(w, h),
            dstBuff = dstData.data,
            srcBuff = window.context.getImageData(0, 0, w, h).data,
            y = h,
            x;
        while (y--) {
            x = w;
            while (x--) {
                let sy = y,
                    sx = x,
                    dstOff = (y * w + x) * 4,
                    r = 0,
                    g = 0,
                    b = 0;
                //a = 0;
                for (let cy = 0; cy < katet; cy++) {
                    for (let cx = 0; cx < katet; cx++) {
                        let scy = sy + cy - half;
                        let scx = sx + cx - half;
                        if (scy >= 0 && scy < h && scx >= 0 && scx < w) {
                            let srcOff = (scy * w + scx) * 4;
                            let wt = weights[cy * katet + cx];
                            r += srcBuff[srcOff] * wt;
                            g += srcBuff[srcOff + 1] * wt;
                            b += srcBuff[srcOff + 2] * wt;
                            //a += srcBuff[srcOff + 3] * wt;
                        }
                    }
                }
                dstBuff[dstOff] = r * mix + srcBuff[dstOff] * (1 - mix);
                dstBuff[dstOff + 1] = g * mix + srcBuff[dstOff + 1] * (1 - mix);
                dstBuff[dstOff + 2] = b * mix + srcBuff[dstOff + 2] * (1 - mix)
                dstBuff[dstOff + 3] = srcBuff[dstOff + 3];
            }
        }
        window.context.putImageData(dstData, 0, 0);
    }

    effectBlurImage(radius, blurAlphaChannel) {
        Render.loadCheckPoint();
        if (isNaN(radius) || radius < 1) return;

        if (blurAlphaChannel)
            this.sb_object.effectBlurCanvasRGBA(0, 0, radius);
        else
            this.sb_object.effectBlurCanvasRGB(0, 0, radius);
    }

    applyFrameBlur(value) {
        let gradientPixels = this.cb_object.getLinearGradientMap(window.canvas.width, window.canvas.height, window.canvas.width * 0.5, window.canvas.height * 0.5, -Math.PI * 0.1, window.canvas.width * 2, true);
        //let outerRadius = Math.sqrt(Math.pow(window.canvas.width / 2, 2) + Math.pow(window.canvas.height / 2, 2));
        //let gradientPixels = getRadialGradientMap(window.canvas.width, window.canvas.height, window.canvas.width * 0.5, window.canvas.height * 0.5, outerRadius / 2, outerRadius);
        this.blurRadius = parseInt(value, 10);
        this.radiusFactor = 1.5;
        this.divider = this.radiusFactor;
        for (let i = 1; i < this.steps; i++) {
            this.divider += Math.pow(this.radiusFactor, i + 1);
        }
        let startRadius = this.blurRadius / this.divider;
        this.cb_object.compoundBlurImage(gradientPixels, startRadius, this.radiusFactor, this.steps, true);
    }

    applyVignette() {
        window.context.save();
        let gradient,
            outerRadius = Math.sqrt(Math.pow(window.canvas.width / 2, 2) + Math.pow(window.canvas.height / 2, 2));
        window.context.globalCompositeOperation = 'source-over';
        gradient = window.context.createRadialGradient(window.canvas.width / 2, window.canvas.height / 2, 0, window.canvas.width / 2, window.canvas.height / 2, outerRadius);
        gradient.addColorStop(0, 'rgba(0, 0, 0, 0)');
        gradient.addColorStop(0.5, 'rgba(0, 0, 0, 0.3)');
        gradient.addColorStop(1, 'rgba(0, 0, 0, 0.6)');
        window.context.fillStyle = gradient;
        window.context.fillRect(0, 0, window.canvas.width, window.canvas.height);
        window.context.globalCompositeOperation = 'lighter';
        gradient = window.context.createRadialGradient(window.canvas.width / 2, window.canvas.height / 2, 0, window.canvas.width / 2, window.canvas.height / 2, outerRadius);
        gradient.addColorStop(0, 'rgba(255, 255, 255, 0.1)');
        gradient.addColorStop(0.5, 'rgba(255, 255, 255, 0)');
        gradient.addColorStop(1, 'rgba(0, 0, 0, 0)');
        window.context.fillStyle = gradient;
        window.context.fillRect(0, 0, window.canvas.width, window.canvas.height);
        window.context.restore();
    }

    applyGrayscale() {
        window.effect = "Grayscale";
        if (!window.isStreaming)
            Render.loadCheckPoint();
        let imageData = window.context.getImageData(0, 0, window.canvas.width, window.canvas.height);
        let dataArray = imageData.data;
        for (let i = 0; i < dataArray.length; i += 4) {
            let red = dataArray[i];
            let green = dataArray[i + 1];
            let blue = dataArray[i + 2];
            let alpha = dataArray[i + 3];
            let gray = (red + green + blue) / 3;
            dataArray[i] = gray;
            dataArray[i + 1] = gray;
            dataArray[i + 2] = gray;
            dataArray[i + 3] = alpha;
        }
        //imageData.data = dataArray;
        Object.defineProperties(imageData, {
            'data': {
                value: dataArray,
                writable: true,
                configurable: true,
                enumerable: true
            }
        });
        window.context.putImageData(imageData, 0, 0);
    }

    applySepia() {
        window.effect = "Sepia";
        if (!window.isStreaming)
            Render.loadCheckPoint();
        let imageData = window.context.getImageData(0, 0, window.canvas.width, window.canvas.height);
        let dataArray = imageData.data;
        for (let i = 0; i < dataArray.length; i += 4) {
            let red = dataArray[i];
            let green = dataArray[i + 1];
            let blue = dataArray[i + 2];
            let alpha = dataArray[i + 3];
            let outRed = (red * .393) + (green * .769) + (blue * .189);
            let outGreen = (red * .349) + (green * .686) + (blue * .168);
            let outBlue = (red * .272) + (green * .534) + (blue * .131);
            dataArray[i] = outRed < 255 ? outRed : 255;
            dataArray[i + 1] = outGreen < 255 ? outGreen : 255;
            dataArray[i + 2] = outBlue < 255 ? outBlue : 255
            dataArray[i + 3] = alpha;
        }
        //imageData.data = dataArray;
        Object.defineProperties(imageData, {
            'data': {
                value: dataArray,
                writable: true,
                configurable: true,
                enumerable: true
            }
        });
        window.context.putImageData(imageData, 0, 0);
    }

    applyInvert() {
        window.effect = "Invert";
        if (!window.isStreaming)
            Render.loadCheckPoint();
        let imageData = window.context.getImageData(0, 0, window.canvas.width, window.canvas.height);
        let dataArr = imageData.data;
        for (let i = 0; i < dataArr.length; i += 4) {
            let r = dataArr[i];
            let g = dataArr[i + 1];
            let b = dataArr[i + 2];
            //let a = dataArr[i + 3];
            let invertedRed = 255 - r;
            let invertedGreen = 255 - g;
            let invertedBlue = 255 - b;
            dataArr[i] = invertedRed;
            dataArr[i + 1] = invertedGreen;
            dataArr[i + 2] = invertedBlue;
        }
        //imageData.data = dataArr;
        Object.defineProperties(imageData, {
            'data': {
                value: dataArr,
                writable: true,
                configurable: true,
                enumerable: true
            }
        });
        window.context.putImageData(imageData, 0, 0);
    }

    applyR(value) {
        value = parseInt(value, 10);
        Render.loadCheckPoint();
        let imageData = window.context.getImageData(0, 0, window.canvas.width, window.canvas.height);
        let data = imageData.data;
        for (let i = 0; i < data.length; i += 4) {
            data[i] = 255 - ((255 - data[i]) * (255 - value * 1) / 255);
        }
        //imageData.data = data;
        Object.defineProperties(imageData, {
            'data': {
                value: data,
                writable: true,
                configurable: true,
                enumerable: true
            }
        });
        window.context.putImageData(imageData, 0, 0);
    }

    applyG(value) {
        value = parseInt(value, 10);
        Render.loadCheckPoint();
        let imageData = window.context.getImageData(0, 0, window.canvas.width, window.canvas.height);
        let data = imageData.data;
        for (let i = 0; i < data.length; i += 4) {
            data[i + 1] = 255 - ((255 - data[i + 1]) * (255 - value * 1) / 255);
        }
        //imageData.data = data;
        Object.defineProperties(imageData, {
            'data': {
                value: data,
                writable: true,
                configurable: true,
                enumerable: true
            }
        });
        window.context.putImageData(imageData, 0, 0);
    }

    applyB(value) {
        value = parseInt(value, 10);
        Render.loadCheckPoint();
        let imageData = window.context.getImageData(0, 0, window.canvas.width, window.canvas.height);
        let data = imageData.data;
        for (let i = 0; i < data.length; i += 4) {
            data[i + 2] = 255 - ((255 - data[i + 2]) * (255 - value * 1) / 255);
        }
        //imageData.data = data;
        Object.defineProperties(imageData, {
            'data': {
                value: data,
                writable: true,
                configurable: true,
                enumerable: true
            }
        });
        window.context.putImageData(imageData, 0, 0);
    }

    applyOverlay(overlay) {
        Render.loadCheckPoint();
        let imageData = window.context.getImageData(0, 0, window.canvas.width, window.canvas.height),
            data = imageData.data;
        let mask = new Image();
        mask.src = "assets/img/" + overlay;
        let maskData;
        mask.onload = function () {
            let tempCa = document.createElement('canvas');
            let width = window.canvas.width;
            let height = window.canvas.height;
            tempCa.width = width;
            tempCa.height = height;
            let tempCo = tempCa.getContext("2d");
            tempCo.drawImage(mask, 0, 0, mask.width, mask.height, 0, 0, width, height);
            let tempData = tempCo.getImageData(0, 0, tempCa.width, tempCa.height);
            maskData = tempData.data;
            for (let i = 0; i < data.length; i += 4) {
                data[i] = data[i] * maskData[i] / 255;
                data[i + 1] = data[i + 1] * maskData[i + 1] / 255;
                data[i + 2] = data[i + 2] * maskData[i + 2] / 255;
            }
            //imageData.data = data;
            Object.defineProperties(imageData, {
                'data': {
                    value: data,
                    writable: true,
                    configurable: true,
                    enumerable: true
                }
            });
            window.context.putImageData(imageData, 0, 0);
        }
    }

}


