import $ from './lib/jquery.min';
require("imports-loader?jQuery=jquery!touchSwipe");
require("imports-loader?jQuery=jquery!perfectScrollbar");
import Render from './Render';
import Cropper from './lib/cropper.min';

export default class Ui {

    static showNewCrop() {
        var image = window.canvas;
        var cropper = new Cropper(window.canvas, {
            background: false,
            viewMode: 0,
            minContainerHeight: $("#new").height(),
            minContainerWidth: $("#new").width()
        });
        window.cropper = cropper;
        image.addEventListener('ready', function () {
            Render.canvasPos();
        });
        $(".setting").each(function () {
            $(this).hide();
        });
        $("#newCrop").show();
        $("#title").text("Crop");
        $('#undo').hide();
        $('#filters-icon').hide();
        $('#package-icon').hide();
        $('#setting-icon').hide();
    }

    static showFilters() {
        Render.saveCheckPoint();
        $(".setting").each(function () {
            $(this).hide();
        });
        $(".theme").each(function () {
            $(this).hide();
        });
        $("#filters").show();
        $('.setting').perfectScrollbar('update');
        $("#title").text("Filters");
        $("#new").swipe("enable");
        $('#undo').show();
        $("#check").hide();
    }

    static showPackage() {
        var test = $("#package-icon").hasClass("active");
        if (test === false)
            Render.saveCheckPoint();
        $("#package").toggle();
        $('.theme').perfectScrollbar('update');
        $("#title").text("Package");
        $(".list-item").on("mouseenter", function () {
            $(this).addClass("active");
        });
        $(".list-item").on("mouseleave", function () {
            $(this).removeClass("active");
        });
    }

    static showSettings() {
        Render.saveCheckPoint();
        $(".setting").each(function () {
            $(this).hide();
        });
        $(".theme").each(function () {
            $(this).hide();
        });
        $("#settings").show();
        $("#title").text("Settings");
        $("#back").hide();
        $('.setting').perfectScrollbar('update');
        $("#new").swipe("disable");
        Render.initTempCanvas();
        $('#undo').show();
        $('#filters-icon').show();
        $('#package-icon').show();
        $('#setting-icon').show();
        $("#check").hide();
    }

    static showCSettings() {
        Render.saveCheckPoint();
        $(".setting").each(function () {
            $(this).hide();
        });
        $(".theme").each(function () {
            $(this).hide();
        });
        $("#c-settings").show();
        $("#title").text("Camera");
        $("#c-back").hide();
        $('.setting').perfectScrollbar('update');
        $("#new").swipe("disable");
    }

    static showSaturation() {
        $(".setting").each(function () {
            $(this).hide();
        });
        $("#saturation").show();
        $('.setting').perfectScrollbar('update');
        $("#title").text("Saturation");
    }

    static showEffects() {
        $(".setting").each(function () {
            $(this).hide();
        });
        $("#effects").show();
        $("#back").show();
        $('.setting').perfectScrollbar('update');
        $("#title").text("Effects");
    }

    static showBrightness() {
        $(".setting").each(function () {
            $(this).hide();
        });
        $("#brightness").show();
        $('.setting').perfectScrollbar('update');
        $("#title").text("Brightness");
    }

    static showContrast() {
        $(".setting").each(function () {
            $(this).hide();
        });
        $("#contrast").show();
        $('.setting').perfectScrollbar('update');
        $("#title").text("Contrast");
    }

    static showVignette() {
        $(".setting").each(function () {
            $(this).hide();
        });
        $("#vignette").show();
        $("#title").text("Vignette");
    }

    static showBlur() {
        $(".setting").each(function () {
            $(this).hide();
        });
        $("#blur").show();
        $("#title").text("Blur");
    }

    static showFlip() {
        $(".setting").each(function () {
            $(this).hide();
        });
        $("#flip").show();
        $("#title").text("Flip");
    }

    static showRotation() {
        $(".setting").each(function () {
            $(this).hide();
        });
        $("#rotation").show();
        $("#title").text("Rotation");
        $('#undo').hide();
    }

    static showSharpen() {
        $(".setting").each(function () {
            $(this).hide();
        });
        $("#sharpen").show();
        $("#title").text("Sharpen");
    }

    static showFrames() {
        $(".setting").each(function () {
            $(this).hide();
        });
        $("#frames").show();
        $('.setting').perfectScrollbar('update');
        $("#title").text("Frames");
        $("#back").show();
    }

    static showTextures() {
        $(".setting").each(function () {
            $(this).hide();
        });
        $("#textures").show();
        $('.setting').perfectScrollbar('update');
        $("#title").text("Textures");
        $("#back").show();
    }

    static showSpecial() {
        $(".setting").each(function () {
            $(this).hide();
        });
        $("#special").show();
        $('.setting').perfectScrollbar('update');
        $("#title").text("Special");
        $("#back").show();
    }

    static showLogo() {
        $(".setting").each(function () {
            $(this).hide();
        });
        $("#logo").show();
        $('.setting').perfectScrollbar('update');
        $("#title").text("Logo");
        $("#back").show();
    }

    static showFrameBlur() {
        $(".setting").each(function () {
            $(this).hide();
        });
        $("#frame-blur").show();
        $("#title").text("Frame Blur");
    }

    static showRgb() {
        $(".setting").each(function () {
            $(this).hide();
        });
        $("#rgb").show();
        $(".tab-footer a").css({
            "width": "20%",
            "display": "table-cell",
            "text-align": "center"
        });
        $("#check").show();
        $("#title").text("RGB");
    }


    static showNoise() {
        $(".setting").each(function () {
            $(this).hide();
        });
        $("#noise").show();
        $("#title").text("Noise");
    }

    static showEditor() {
        $(".interaction").show();
        $("#undo").show();
        $("#check").show();
        $("#export-btn").show();
        $("#refresh-btn").show();
        $("#camera-btn").show();
        $("#gallery-btn").show();
        $("#take").hide();
        $(".back").show();
        $("#c-back").hide();
        $("#back").hide();
        $("#filters").removeClass("toright");
        $("#filters-icon").trigger("click");
    }

}


