import Render from './Render';

export default class Api
{
    static upload() {
        var imageUp = document.getElementById("imageUp");
        if (imageUp) {
            imageUp.click();
        }
    }

    static getImage(files) {
        if (files.length) {
            var file = files[0];
            window.image.src = window.URL.createObjectURL(file);
            window.image.onload = function () {
                Render.restore();
            }
        }
    }

    static dragenter(e) {
        e.stopPropagation();
        e.preventDefault();
    }

    static dragover(e) {
        e.stopPropagation();
        e.preventDefault();
    }

    static drop(e) {
        e.stopPropagation();
        e.preventDefault();
        var dt = e.dataTransfer;
        var files = dt.files;
        Api.getImage(files);
    }

    static getCamera() {
        navigator.getMedia = ( navigator.getUserMedia || navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator.msGetUserMedia);
        if (navigator.getMedia) {
            navigator.getMedia(
                {video: true,audio: false},
                function (stream) {
                    var vendorURL = window.URL || window.webkitURL;
                    window.video.src = vendorURL.createObjectURL(stream);
                    window.video.onloadedmetadata = function() {
                        window.video.play();
                    };
                    window.streaming = stream;
                },
                function (err) {
                    console.log("An error occured! " + err);
                }
            );
        } else {
            console.log("getUserMedia not supported");
        }
        window.brightnessV = 0;
        window.contrastV = 0;
        window.saturationV = 1;
        window.noiseV = 0;
        window.vignette = false;
        window.effect = 'none';
        window.current = 'Original';
    }

    static importImage() {
        clearInterval(window.streamInterval);
        window.streaming.getTracks()[0].stop();
        window.isStreaming = false;
        window.image.src = window.canvas.toDataURL();
        window.image.onload = function () {
            Render.restore();
        }
    }

}


